<template>
  <div class="mui-content">


    <!--<form id='login-form' class="mui-input-group">-->
      <!--<div class="mui-input-row">-->
        <!--<label><span class="mui-icon iconfont icon-wode"></span></label>-->
        <!--<input id='account' type="text" class="mui-input-clear mui-input" placeholder="" v-model="username">-->
      <!--</div>-->
      <!--<div class="mui-input-row">-->
        <!--<label><span class="mui-icon mui-icon-locked"></span></label>-->
        <!--<input id='password' type="password" class="mui-input-clear mui-input" placeholder="" v-model="password">-->
      <!--</div>-->
    <!--</form>-->
    <!--<div class="mui-content-padded">-->
      <!--<button id='login' class="mui-btn mui-btn-block mui-btn-primary" @click="login"></button>-->

    <!--</div>-->
    <!--<div class="mui-content-padded oauth-area">-->

    <!--</div>-->
    <!--<div class="mui-content-padded">-->
      <!--<a id='forgetPassword' @click="forgetPassword"></a>-->
      <!--<a id='register' @click="register"></a>-->
    <!--</div>-->

    <div class="login_bg">
      <img src="../assets/img/login_bg.png">
      <div>
        <div class="login_title" style="display: none;">
          <a href="index.html">
            <i class="icon"></i>
          </a>
          <a href="reg.html">
            <span></span>
          </a>
        </div>
        <!---->
        <div class="login_content">
          <div class="username">
            <i class="icon icon_user"></i>
            <input type="text" id="user" v-model="username" placeholder="/"/>
          </div>
          <div class="password">
            <i class="icon icon_pwd"></i>
            <input type="password" id="pwd" v-model="password" class="mui-input-password" placeholder=""/>
          </div>
          <span class="btn login_btn" @click="login"></span>
          <p class="forget_btn">
            <a href="forgetPwd.html" class="forget-a">?</a>
            <a href="/#/register" class="register-a"></a>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data () {
      return {
        msg: '',
        username: '',
        password: ''
      }
    },
    methods: {

      login () {
        //
        let phoneReg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
        if (!phoneReg.test(this.username)) {
           this.mui.alert('', '');
           return
        }
        if(this.password == "" || this.password.length < 6){
          this.mui.alert('6-20', '');
          return
        }


        this.$http.post(this.$store.state.basePath + '/user/user/login',
          {
            phone: this.username,
            password: this.password,
          },
        ).then(
          (res) => {
            if (res.body.status == 0) {
              console.log(res)
              this.mui.alert(res.body.msg, '')
            } else {

              this.$router.replace('/appIndex')
              console.log(res)
            }

          })
      }
    },
    created () {

    }
  }
</script>
